body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(175, 117, 223);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face { 
  font-family: "ObelixProRegular";
  src: url("/fonts/ObelixProRegular.eot");
  src: url("/fonts/ObelixProRegular.eot?#iefix")format("embedded-opentype"),
  url("/fonts/ObelixProRegular.woff") format("woff"),
  url("/fonts/ObelixProRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Aerodynamic Bold';
  src: local('Aerodynamic Bold'), local('Bebas-Neue-Regular'),
      url('/fonts/Aerodynamic Bold.woff2') format('woff2'),
      url('/fonts/Aerodynamic Bold.woff') format('woff'),
      url('/fonts/Aerodynamic Bold.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Days One';
  src: local('Days One Regular'), local('Days-One-Regular'),
      url('/fonts/DaysOne-Regular.woff2') format('woff2'),
      url('/fonts/DaysOne-Regular.woff') format('woff'),
      url('/fonts/DaysOne-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Lack';
  src: local('Lack Regular'), local('Lack-Regular'),
      url('/fonts/Lack-Regular.woff2') format('woff2'),
      url('/fonts/Lack-Regular.woff') format('woff'),
      url('/fonts/Lack-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
.content{
  width: 80%;
  margin: 0 auto;
}
.header{
  margin-top: 2vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  font-family: ObelixProRegular;
  font-size: 25px;
  color: rgb(255, 255, 255);
  margin-bottom: 15px;
}
.rightPart{
  display: flex;
  flex-direction: row;
}
.headerText{
  margin-right: 2vw;
}
.goods{
  display: grid;
  grid-template-columns: repeat(
    auto-fill, minmax(350px, 1fr)
  );
  gap: 1rem;
  margin-bottom: 100px;

}
.card{
  background-color:yellow;
  display: flex;
  flex-direction: column;
  width: 350px;
  box-shadow: 0px;
}
.cardImage{
  height: 350px;
  background-color: red;
}
.cardContent{
  display: flex;
  flex-direction: column;
  border: 3.5px solid black;
  border-top: 0px;
  font-family: ObelixProRegular;
  font-size: 12px;
  vertical-align: middle;
}
.slogan{
  margin-left: auto;
  margin-right: auto;
}
img{
  width: 350px;
}
button{
  background-color: black;
  border: 0px;
  font-size: 20px;
  color: white;
  padding: 3px 15px;
  cursor: pointer;
  font-family: ObelixProRegular;
}
button:focus {
  outline: none;
  background-color: #3a3a3a;
}

.cart-quantity{
  padding-left: 5px;
}
.buttonText{
  vertical-align: middle;
  margin: 10px 0px 6px 0px;
}
.basket-list{
  position: fixed;
  top:50%;
  left:50%;
  transform: translate(-50%,-50);
  background-color: rgb(154, 42, 42);
  width: 50rem;
  max-width: 95%;
  box-shadow: 1px 3px 8px 5px aquamarine;
}
.basket-clear {
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #fff;
}
.basket-quantity {
  z-index: 20;
  cursor: pointer;
  vertical-align: middle;
  color: #4ddee1;
  font-size: 20px;
}
.collection .collection-item {
    background-color: #fff;
    line-height: 1.5rem;
    padding: 10px;
    margin: 0;
    border-bottom: 1px solid #e0e0e0;
}
.shopInfo{
  background-color: yellow;
  color: black;
  padding: 5px 0px 5px 10px;
  font-family: 'Days One';
  font-size: 15px;
}
.collectionItem_active{
  background-color: yellow;
  padding: 5px 0px 5px 10px;
  color: rgb(0, 0, 0);
  font-family: 'Lack';
  font-size: 18px;
}

.collectionItem{
  background-color: white;
  border: 1px black solid;
  font-family: ObelixProRegular;
  font-size: 18px;
}
.basket-quantity{
  color: black;
  font-family: 'Days One';
  display: flex;
  align-items: center;
  padding: 0px 10px;
  font-size: 16px;
  
}
.footer{
  background-color: rgba(0, 0, 0, 0.304)
}
.footerText{
  font-family: 'Days One';
  font-size: 15px;
  margin:2px;
  color: white;

}
.footerText_data{
  padding-right:4px ;
}